import SWAL from 'sweetalert2'
import { format } from 'date-fns'
import UI_ELEMENTS from './ui_elements'
import USER from '../MODEL/userInfo'
import SOCKET from '../CONTROLLER/ws'
import { getMessages } from '../CONTROLLER/getMessages'

const messageOnScroll = (block,array) => {
	if(!array.length){
		SWAL.fire({title:'Вся история загружена'})	
		return
	}
	for(let i = 0;i<20;i++){
		const message = array.shift()
		block.prepend(messageAdding(message.text,message.createdAt,message.user,'delivered'))
	}
}

export const wrapView = async () => {
	console.log(USER)
	const history = await getMessages()
	console.log(history)
	if (!history){
		return
	}
	const mainView = document.querySelector('.main-view')
	const div = document.createElement('div')
	div.classList.add('main-view')
	
	messageOnScroll(div,history.messages)
	// div.prepend(messageAdding(text,createdAt,user,'delivered'))
	div.addEventListener('scroll',(event)=>{
		const scrollTop = event.target.scrollTop
		if(scrollTop === 0 ){
			messageOnScroll(div,history.messages)
		}
	})
	
	UI_ELEMENTS.MAIN.replaceChild(div,mainView)
	div.scrollTop = div.scrollHeight
}


wrapView()

export const messageAdding = 
(
	message,
	time = Date.now(), 
	sender = USER,
	condition='sending'
) => {
	const messageFragment = UI_ELEMENTS.MESSAGE_TEMPLATE.content.cloneNode(true)
	const messageText =  messageFragment.querySelector('.main-view-message_text')
	const login = messageFragment.querySelector('.main-view-message_text_user')
	login.textContent = `${sender.name} : `
	messageText.textContent = message
	messageText.prepend(login)
	const messageBlock = messageFragment.querySelector('.main-view-message')
	messageBlock.classList.add(condition)
	messageBlock.dataset.email = sender.email
	const position = sender.email === USER.email ? 'user' : 'companion'
	messageBlock.classList.add(position)
	const messageTime = messageFragment.querySelector('.main-view-message_time')
	messageTime.textContent = format(new Date(time),'HH:mm')
	return messageFragment
}

const socket = SOCKET()

const messageSend = (event) => {
	const mainView = document.querySelector('.main-view')
	event.preventDefault()
	const messageText = event.target.querySelector('.main-form_input')
	
	if(messageText.value.length < 1){
		SWAL.fire({
			position: 'top-end',
			icon: 'error',
			title: 'Введите сообщение',
			showConfirmButton: false,
			timer: 700
		})
		return	
	}
	const userMessage = messageAdding(messageText.value.trim())
	mainView.append(userMessage)
	
	socket.send(JSON.stringify({ text: messageText.value.trim() }) )
	messageText.value = ''
	
	
}

socket.onmessage = (	event	)	=>	{
	const messageInfo = JSON.parse(	event.data	)
	const mainView = document.querySelector('.main-view')
	const sendingMessage = mainView.querySelector('.sending')
	if(sendingMessage){
		const sendingText = sendingMessage.querySelector('.main-view-message_text').textContent.slice(USER.name.length+3)
		const sendingTime = sendingMessage.querySelector('.main-view-message_time').textContent
		const sendingEmail = sendingMessage.dataset.email
		const timeCondition = sendingTime === format(new Date(messageInfo.createdAt),'HH:mm')

		if(sendingEmail === messageInfo.user.email && sendingText === messageInfo.text && timeCondition)	{
			sendingMessage.classList.remove('sending')
			sendingMessage.classList.add('delivered')
			mainView.scrollTop = mainView.scrollHeight
			return
		}
	}
	mainView.append(messageAdding(	messageInfo.text,messageInfo.createdAt,messageInfo.user,'delivered'	)	)
	mainView.scrollTop = mainView.scrollHeight

}

socket.onclose = () =>	{
	SWAL.fire({title:'Соединение с чатом потеряно,перезагрузите страницу!',
		preConfirm:setTimeout(()=>{
			window.location.reload()
		},2000),
		showLoaderOnConfirm:true}
	)
}








UI_ELEMENTS.MESSAGE_SEND_FORM.addEventListener('submit',messageSend)

